import { store } from '@risingstack/react-easy-state'
import { printRoute } from './print-route'

export namespace Order {

  export interface Address {
    street: string,
    city: string,
    house_number?: string | number
  }
  
  export interface Contact {
    first_name?: string,
    last_name?: string,
    phone?: string
  }
  
  export interface Station {
    address: Address,
    contact: Contact
  }
  
  export interface KeyStation {
    is_origin: boolean,
    address: Address
  }
  
}

const orders = store({
  printRoute
})

export default orders