import { Order } from './'

interface PrintRouteParams {
  stations: Order.Station[],
  key_station?: Order.KeyStation
}

interface PrintRouteResult {
  origin: Order.Address,
  originText: string,
  waypoints?: Order.Address[],
  waypointsText?: string,
  destination?: Order.Address,
  destinationText?: string,
  routeText: string
}

const getText = ({
  street,
  city,
  house_number
}: Order.Address) : string => {
  let str = `${street}`
  if (house_number) {
    str = `${str} ${house_number}, `
  }
  str = `${str} ${city}`
  return str
}

export const printRoute = ({
  stations,
  key_station
}: PrintRouteParams) : PrintRouteResult => {

  // define origin and destination
  let origin: Order.Address,
  destination: Order.Address | undefined,
  originText: string,
  destinationText: string | undefined,
  waypoints: Order.Address[] | undefined,
  waypointsText: string | undefined,
  routeText: string

  if (key_station) {
    origin = !key_station.is_origin
    ? stations[0].address
    : key_station.address
    destination = !key_station.is_origin
    ? key_station.address
    : stations[stations.length - 1].address
  } else {
    origin = stations[0].address
  }

  originText = getText(origin)
  if (destination) {
    destinationText = getText(destination)
  }

  // get waypoints
  if (stations.length > 1) {
    waypoints = JSON.parse(JSON.stringify(stations))
    if (!key_station.is_origin) {
      waypoints.shift()
    } else {
      waypoints.pop()
    }
    waypointsText = ''
    waypoints.map(wp => {
      waypointsText = `${waypointsText} + ${getText(wp)}`
    })
  }

  // routeText
  routeText = originText

  if (destinationText) {
    routeText = `${routeText} - ${destinationText}`
  }

  return {
    origin,
    originText,
    destination,
    destinationText,
    routeText
  }

}