import React from 'react'
import { view } from '@risingstack/react-easy-state'

const ClientPrice = ({ order }) => {

  return order.client_price ? (
    <span>
      { order.client_price.subtotal }
      {
        order.client_price.missingPrices && (
          <React.Fragment>
            &nbsp;
            ({order.client_price.missingPrices})
          </React.Fragment>
        )
      }
    </span>
  ) : <span>*</span>
}

export default view(ClientPrice)