import React from 'react'
import { view } from '@risingstack/react-easy-state'
import { Grid, Paper, makeStyles } from '@material-ui/core'
import { printDate } from '../../common'
import { report, language } from '../../stores'

const useStyles = makeStyles({
  paper: {
    padding: '2%',
    textAlign: 'center',
    color: 'black'
  },
  recipient: {
    textDecoration: 'underline'
  }
})

const Wrapper = () => {

  const classes = useStyles()

  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <p>
          {language.t['Report by']}:
        </p>
        <h3>
          {
            report.data.issuer.name
          }
          &nbsp;&mdash;&nbsp;
          {
            report.data.issuer.address.street
          }
          &nbsp;
          {
            report.data.issuer.address.house_number
          }
          &nbsp;&mdash;&nbsp;
          {
            report.data.issuer.phone
          }
        </h3>
        <p>
          {language.t['For']}:
        </p>
        <h3>
          {
            report.data.receiver.name
          }
          {
            report.data.receiver.phone && (
              <React.Fragment>
                &nbsp;&mdash;&nbsp;
                { report.data.receiver.phone }
              </React.Fragment>
            )
          }
        </h3>
        <p>
          {language.t['For a period']}:
        </p>
        <p>
          {language.t['Start date']}:&nbsp;
          <strong>
            {printDate({ date: new Date(report.data.start_of_month_ts) })}
          </strong>
          <br></br>
          {language.t['End date']}:&nbsp;
          {
            report.data.end_of_month_ts ? (
              <strong>
                {printDate({ date: new Date(report.data.end_of_month_ts) })}
              </strong>
            ) : (
              <strong>
                { language.t['Not finished yet'] }
              </strong>
            )
          }
          
        </p>
      </Paper>
    </Grid>
  )
}

export default view(Wrapper)